body{
  overflow: hidden;

}

.chatroom{
 justify-content: stretch;
 margin-top:250px;
 width: 450px;
 overflow:auto;
 margin-left: 220px;
 float:left;
 overflow: auto;
 line-height:1.7;
}
.flex-container {
  display: flex;
  flex-direction: row;
  width: 110%;
}

.input-field{
  width:580px;
  height:45px;
  margin-left:150px;
  padding:2px 0;
  border-radius: 50px;
}

.input-button{
  position: relative;
  display: inline-flex;
}

.send{
  position: absolute;
  top:13px;
  right:10px;
}

.channel-list{
  line-height: normal;
}

.signup-form{
  height: 100vh;
}

.signout{
  margin-left:200px;
}

.signin{
  margin:150px;
}

.message__author{
  font-size:20px;
  color:green
}

.message{
font-weight:bold
}
